.SearchBox {
  resize: none; }

textarea {
  resize: vertical;
  max-height: 300px;
  min-height: 40px;
  color: #959595; }

body {
  font-family: "Helvetica Neue", Helvetica, "Varela Round", sans-serif; }

h1, h2, h3, h4, h5 {
  color: #959595; }

/*
==========================
HEADER
==========================
*/
.Bookmarks-Header {
  background-color: #ffea6c;
  top: 0px;
  right: 0px;
  width: 300px;
  position: fixed;
  height: 45px;
  -webkit-filter: blur(0.6px);
          filter: blur(0.6px);
  opacity: .91; }

.Bookmarks-Body {
  background-color: #cecece;
  top: 45px;
  right: 0px;
  width: 300px;
  position: fixed;
  height: 100%;
  overflow: auto; }

.Bookmarks-Title-Text {
  width: 260px;
  height: 40px;
  font-size: 16px;
  font-weight: bold;
  letter-spacing: 1.6px;
  text-align: left;
  color: #ffffff;
  padding-left: 20px;
  padding-top: 5px; }

/*
==========================
NOTES
==========================
*/
.Bookmarks {
  border: solid 0.2px #959595;
  color: #959595; }

/*
==========================
SEARCH
==========================
*/
.SearchBox {
  width: 284px;
  height: 40px;
  -webkit-box-shadow: none;
          box-shadow: none;
  border: 1px solid #e9e9e9;
  margin-top: 35px;
  margin-left: 8px;
  margin-right: 8px;
  -ms-flex-align: center;
      align-items: center;
  color: #959595;
  resize: none;
  padding: 0px;
  position: relative;
  z-index: 1; }

.SearchBoxText {
  padding-top: 15px;
  font-size: 12px;
  letter-spacing: 2px;
  text-align: center; }

/*
==========================
BUTTONS
==========================
*/
.bookmarksExitButton {
  border-radius: 50%;
  width: 25px;
  height: 25px;
  top: 10px;
  right: 20px;
  color: #ffea6c;
  position: fixed;
  display: inline-block;
  font-size: 16px;
  font-weight: 700;
  font-style: bold;
  text-align: center;
  border: none;
  background-color: none;
  background: rgba(255, 255, 255, 0); }
  .bookmarksExitButton:hover {
    background: -webkit-gradient(linear, left top, left bottom, color-stop(20%, #f3f3f3), to(#ffffff));
    background: -webkit-linear-gradient(top, #f3f3f3 20%, #ffffff 100%);
    background: -o-linear-gradient(top, #f3f3f3 20%, #ffffff 100%);
    background: linear-gradient(to bottom, #f3f3f3 20%, #ffffff 100%);
    background-color: #f6f6f6; }



