textarea {
  resize: vertical;
  max-height: 300px;
  min-height: 40px;
  color: #959595; }

body {
  font-family: "Helvetica Neue", Helvetica, 'Varela Round', sans-serif; }

h1, h2, h3, h4, h5 {
  font-family: "Helvetica Neue", Helvetica, 'Varela Round', sans-serif;
  color: #ffffff; }

.container {
  text-align: center; }

/*
==========================
CURRENT WEATHER
==========================
*/
.currentWeather {
  background-color: #99dcff;
  top: 0px;
  right: 0px;
  width: 19rem;
  position: fixed;
  height: 50vh; }

.currentTempMinMax {
  border: solid 0.1em #ffffff;
  border-left: hidden;
  border-right: hidden; }

/*
==========================
FORECAST
==========================
*/
.forecast {
  position: fixed;
  background-color: #99dcff;
  top: 50vh;
  right: 0;
  height: 60vh;
  width: 19rem; }



